"use client";

import { useEffect } from "react";

/**
 * This component suppresses annoying hydration warnings caused by browser extensions
 * (like Grammarly, LastPass, or antivirus software) injecting attributes into the DOM.
 * Specifically targets 'bis_skin_checked' which is common.
 */
export default function HydrationOverlayFix() {
  useEffect(() => {
    if (typeof window !== "undefined") {
      const originalError = console.error;
      
      console.error = (...args) => {
        // Safe check for the error message content
        if (args.length > 0 && typeof args[0] === 'string') {
          // Suppress "bis_skin_checked" mismatch warning
          if (args[0].includes("bis_skin_checked")) {
            return;
          }
          // Suppress the generic hydration mismatch if it is likely caused by the above
          // Note context: strict filtering to avoid hiding real errors
          if (args[0].includes("Hydration failed because the initial UI does not match")) {
             // We can't be 100% sure it's the extension, but often is.
             // For now, let's only filter the explicit prop mismatch.
          }
        }
        
        originalError.apply(console, args);
      };
    }
  }, []);

  return null;
}
